<template>
<div>
    <!-- header start -->
    <cart-header></cart-header>
    <!-- header end -->
    <!-- productList start -->
    <cart-body></cart-body>
    <cart-footer></cart-footer>    
    <!-- modal end -->
</div>
  
    
</template>
<style lang="css">
    @import '../../assets/css/cart.css';
</style>


<script>
import Axios from "axios";
import {mapGetters,mapActions} from 'vuex';

import Header from './Header';
import Body from './Body';
import Footer from './Footer'

export default {
  name: "cart",
  data() {
    return {
      product: [],/*  */
      /*  */
      editArr:[],/*  */
      selectIt:[],/*  */
      thisIndex:'',/*  */
      seen:false,/*  */
      sll:false,/*  */
      totalPrice:0/*  */
    };
  },
  methods: {
      ...mapActions(['getDatas']),
    selectThem(index){/*  */
        var its=this.selectIt;
        its[index]?this.$set(its,index,false):this.$set(its,index,true);
    },
    editItems(index){/*  */
        var edit=this.editArr;
        edit[index]?this.$set(edit,index,false):this.$set(edit,index,true);
    },
    decNum(index){/*  */
        if (this.product[index].p_num <= 1) {
                if (confirm('')) {
                    this.remove(index);
                }
                return;
            }
            this.product[index].p_num--;
    },
    remove(idx) { /*  */
            if (idx == -2) {
                this.product = [];
                return;
            }
            this.$set(this.editArr,idx,false);
            this.seen=false;
            this.selectIt[idx]=false;
            this.product.splice(idx, 1);
            this.thisIndex="";
            this.cartNum=this.product.length;
    },
    addNum(idx){/*  */
        this.product[idx].p_num++;
    },
    selectAll(){/*  */
        let converts=false;
        let datas=this.selectIt;
        let sll=this.sll;
        if(sll===true){
            converts=false;
        }else if(sll===false){
            converts=true;
        }
        for(var i=0;i<datas.length;i++){
            this.$set(datas,i,converts);
        }
        this.sll?this.sll=false:this.sll=true;
    }
  },
  beforeCreate(){
     
  },
  created() {/*   */
     this.getDatas();
  },
    watch: {/*  */
        toCounts: function (val, oldVal) {
            this.totalPrice = val;
        }
    },
    computed: {
        toCounts: function () {
            var _this = this;
            var selectIt=this.selectIt;
            _this.totalPrice = 0;
            _this.product.forEach(function (item,index) {
                console.log(index,item)
                if(selectIt[index]){
                    _this.totalPrice += item.p_now_price * item.p_num;
                }
                
            })
            return _this.totalPrice;
        },
        ...mapGetters(['cartNum'])
    }
    ,components:{
        'cart-header':Header,
        'cart-body':Body,
        'cart-footer':Footer
    }
};
</script>



